import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Homepage from '@/components/homepage/Homepage'
import HomepageList from '@/components/homepage/HomepageList'
import Mine from '@/components/mine/Mine'
import MineList from '@/components/mine/MineList'
import Find from '@/components/find/Find'
import FindList from '@/components/find/FindList'
import Recommend from '@/components/recommend/Recommend'
import RecommendList from '@/components/recommend/RecommendList'
Vue.use(Router)

export default new Router({
 // mode:'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/homepage'
    },{
      path:'/homepage',
      name:'Homepage',
      component:Homepage,
      children:[{
        path:'/homepage',
        name:'HomepageList',
        component:HomepageList,
      }]
    },{
      path:'/mine',
      name:'Mine',
      component:Mine,
      children:[{
        path:'/mine',
        name:'MineList',
        component:MineList,
      }]
    },{
      path:'/find',
      name:'Find',
      component:Find,
      children:[{
        path:'/find',
        name:'FindList',
        component:FindList,
      }]
    },{
      path:'/recommend',
      name:'Recommend',
      component:Recommend,
      children:[{
        path:'/recommend',
        name:'RecommendList',
        component:RecommendList,
      }]
    }
  ]
})



