<template>
    <div>
        <input type="text" id="find" placeholder="请输入书名">
        <div id="lunbo">
            <img src="static/img/31.jpg">
            <img src="static/img/32.jpg">
            <img src="static/img/33.jpg">
        </div>
        <div id="screen"><img src="static/img/28.png"> 搜索筛选▼ </div>
        <div>热门搜索</div>
        <div id="hot">
            <a href="">考研</a>
            <a href="">心理学</a>
            <a href="">数学</a>
            <a href="">C语言</a>
            <a href="">追风筝的人</a>
            <a href="">韩寒</a>
            <a href="">平凡的世界</a>
        </div>
        <ul id="history">
            <li><img src="static/img/27.png"/><a href="">服装设计</a><span class="remove">x</span></li>
            <li><img src="static/img/27.png"/><a href="">机械加工制造</a><span class="remove">x</span></li>
            <li><img src="static/img/27.png"/><a href="">从你的全世界路过</a><span class="remove">x</span></li>
        </ul>
        <ul>
            <li id="remove">清除搜索历史</li>
        </ul>






    </div>
</template>

<script>
export default {
    mounted() {
        

        var now = 0;
        function play(){   
        var oImgs = document.getElementById("lunbo").getElementsByTagName("img");
        for(var i=0;i<oImgs.length;i++){
            oImgs[i].className="";
        }
            oImgs[now].className ="on";
            now++;
            if(now == oImgs.length){
                now = 0;
            }
        }
        setInterval(play, 1500);


        var remove = document.getElementById("remove");
        var history = document.getElementById("history");
        remove.onclick=function(){
            history.style.display="none";
            remove.style.display="none";
        }
        

        


    }
}
</script>

<style scopend>
    #lunbo{
        width: 100%;
        height: 2.5rem;
    }
    #lunbo img{display: none;width: 100%;height: 2.5rem;;}
    #lunbo img.on{display: block;}
    #find{
        margin-left: 1.5%;
        height: 0.5rem;
        width: 95%;
        background: #CCCCCC;
        border-radius: 5% 5%;
        text-align: center;
    }
    
    #screen{
        text-align: center;
        width: 100%;
        height: 1rem;
        background: white;
        line-height: 1rem;
        font-size: 18px;
        border-bottom: 1px solid #F6F6F6;
    }
    #screen img{
        height: 1rem;
    }
    #history li{
        width: 100%;
        height: 0.7rem;
        line-height: 0.7rem;
        background: white;
    }
    #history li span{
        float:right;
        position: relative;
        right: 4%;
    }
    #history img{
        height: 0.7rem;
    }
    #history li a{
        color: gray;
        border-bottom: 1px solid #F6F6F6;
    }
    #hot a{
        color: black;
        padding: 4%;
    }
    #remove {
        text-align: center;
        color: #6FACE1;
    }
</style>



