<template>
    <div>
        <div id="headportrait">
            <img src="static/img/30.jpg" id="headpic">
            <div id="info">
                <span id="name">哼哼哈兮</span><br>
                <span id="tab"><img src="static/img/29.png"/> 文艺青年 作者</span>
            </div>
        </div>

        <div class="minelist" v-for="mine in minelist" :key="mine.id">
            <img :src="mine.pic" />
            <span class="txt">{{mine.txt}}</span>
            
        </div>

    </div>
</template>

<script>
import Axios from "axios"
export default {
    data(){
        return{
            minelist:[],
        }
    },
    mounted(){
        let url2 = "static/data/mine.json"
        Axios.get(url2).then((res)=>{
            this.minelist=res.data.data.meun;
        }).catch() 

    }
    
}
</script>

<style scopend>
body{
    background: #EEEEEE;
}
    #headportrait{
        height: 2rem;
        padding-top: 0.5rem;
        background: white;
    }
    .minelist{
        height: 1rem;
        width: 95%;
        padding-left: 5%;
        background: white;
        border-bottom:1px solid #F7F7F7; 
        line-height: 1rem;
        float: left;
    }
    .minelist img{
        height: 6vw;
        margin-top: 5vw;
        float: left;
        padding-right: 2%;
    }
    .minelist .txt{
        float: left;
    }
    .minelist .jump {
        float: right;
    }
    #tab img{
        height: 18px;
    }
    #headpic{
        margin-left: 15%;
        width: 20%;
        border-radius: 50%;
        
    }
    #info{
        float: right;
        margin-right: 28%;
        line-height: 0.5rem;
    }
    #name{
        font-size: 18px;
    }
    #tab img{
        padding-top:1.5vw; 
    }
    
    
</style>



