<script>
export default {
    mounted(){

        var now = 0;
        function play(){   
        var oImgs = document.getElementById("rcdlunbo").getElementsByTagName("img");
        for(var i=0;i<oImgs.length;i++){
            oImgs[i].className="";
        }
            oImgs[now].className ="on";
            now++;
            if(now == oImgs.length){
                now = 0;
            }
        }
        setInterval(play, 1500);
    }
}
</script>

<style scopend>
body{background: #EEEEEE}
    #rcdlunbo{
        width: 100%;
        height: 46vw;
    }
    #rcdlunbo img{display: none;width: 100%;height: 3rem;}
    #rcdlunbo img.on{display: block;}
    .rcd{width: 92%;
        height: 1rem;
        line-height: 1rem;
        background: white;
        padding-left: 4%;
        padding-right: 4%;
        margin-bottom: 2vw;
        }
        .rcd span {
            float: right;
        }
    .rcd img{
        height: 1rem;
    }
    #well{
        width: 100%;
        height: 2.2rem;
