<template>
<div>
    <div id="carousel">
        <img src="static/img/12.png">
        <img src="static/img/13.png">
        <img src="static/img/14.png">
        <img src="static/img/15.png">
        <img src="static/img/16.png">
        <img src="static/img/17.png">
        <img src="static/img/18.png">
    </div>
    <div id="meun">
        <div class="meun" v-for="tt in homepagelist" :key="tt.id" >
            <img :src="tt.pic" />
            <p>{{tt.txt}}</p>
        </div>
        
    </div>
    <div id="list">
        <ul>
            <li v-for="aa in homepagelist1" :key="aa.id">
                <img :src="aa.pic" />
                {{aa.txt}}
                </li>
        </ul>
    </div>
    <div id="finish"></div>
    
</div>    
</template>

<script>
import Axios from "axios"
export default {
    data(){
        return{
            homepagelist:[],
            homepagelist1:[],
        }
    },
    mounted(){

        var now = 0;
        function play(){   
        var oImgs = document.getElementById("carousel").getElementsByTagName("img");
        for(var i=0;i<oImgs.length;i++){
            oImgs[i].className="";
        }
            oImgs[now].className ="on";
            now++;
            if(now == oImgs.length){
                now = 0;
            }
        }
        setInterval(play, 1500);
          
        let url = "./static/data/homepageimg.json"
        Axios.get(url).then((res)=>{
            this.homepagelist=res.data.data.meun;
        }).catch() 

        let url1 = "./static/data/homepageimg2.json"
        Axios.get(url1).then((res)=>{
            this.homepagelist1=res.data.data.meun;
        }).catch() 

    }

    
    
}
</script>

<style scopend>
body{background: #EEEEEE}
    #carousel{
        width: 100%;
        height: 46vw;
    }
    #carousel img{display: none;width: 100%}
    #carousel img.on{display: block;}
    #meun{
        width: 100%;
        margin-bottom: 2vw;
        background: white;
        height: 50vw;
        text-align: center;
        }
    #meun div{width: 30%;
        height: 25vw;
        float: left;
        margin: 1%;
    }
    .meun img{width: 50%;}
    #list{
        margin-top: 5vw;
        background: white;
        width: 95%;
        padding-left: 4%;
    }
    #list li{
        background: white;
        
        height: 1rem;
        line-height: 1rem;
        font-size: 15px;
    }
    #list img{
        margin-top: 2vw;
    }
    #finish{
        width: 100%;
        height: 1rem;
    }
    
</style>



